// owl carousel
.owl-nav {
  > * {
    position: absolute;
    top: 50%;
    width: 60px;
    height: 60px;
    margin-top: -30px;
    background: rgba(255,255,255,.4);
    font-size: 0;
    color: transparent;
    text-align: center;
    &:before {
      content: '';
      display: inline-block;
      width: 20px;
      height: 30px;
      margin-top: 15px;

    }
  }
  .disabled {
    opacity: .1;
    cursor: default !important;
  }
  .owl-prev {
    right: 100%;
    &:before {
      transform: translate(-4px, 0);
    }
  }
  .owl-next {
    left: 100%;
    &:before {
      transform: translate(4px, 0) rotate(180deg);
    }
  }
  &.disabled {
    display: none;
  }
}

  .owl-nav {position:absolute;top:50%;margin-top:rem(-20px);left:0px;width:rem(920px);
    & > * {background:none;}
    .owl-prev {
      position:absolute;
      width:rem(21px);
      height:rem(40px);
      left:-40px;margin:0;
      background-image:url('data:img/png;base64,');
      background-size:cover;
      &:before {content:none;}
    } 
    .owl-next {
      position:absolute;
      width:rem(21px);
      height:rem(40px);
      right:-40px;margin:0;
      background-image:url('data:img/png;base64,');     background-size:cover;
      background-size:cover;
      &:before {content:none;}
    }
  }
